html {
  box-sizing: border-box;
}
body {
  font-family: 'Montserrat', sans-serif;
  background-color: white;
  color: black;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
p {
  margin: 0;
}
img {
  display: block;
}
a {
  text-decoration: none;
}
